import Axios from "axios";
import {get,post} from "./http";

//====================歌手相关======================
//查询歌手
export const getAllSinger =() => get(`singer/allSinger`);
//根据歌手的性别查询
export const getSingerOfSex =(sex) => get(`singer/singerOfSex?sex=${sex}`);

//====================歌曲相关======================
//根据歌手id查询歌曲
export const songOfSingerId = (id) => get(`song/singer/detail?singerId=${id}`);
//根据歌曲id查询歌曲对象
export const songOfSongId = (id) => get(`song/songOfSongId?id=${id}`);
//根据歌手名字模糊查询歌曲
export const likeSongOfName = (name) => get(`song/likeSongOfName?songName=${name}`);

//====================歌单相关======================
//查询歌单
export const getAllSongList =() => get(`songList/allSongList`);
//title模糊查询歌单列表
export const getSongListOfLikeTitle = (title) => get(`songList/likeTitle?title=${title}`);
//style模糊查询歌单列表
export const getSongListOfLikeStyle = (style) => get(`songList/likeStyle?style=${style}`);

//====================歌单的歌曲相关======================
//根据歌单id查询歌曲列表
export const ListSongDetail = (songListId) => get(`listSong/detail?songListId=${songListId}`);

//====================用户相关======================
//查询用户
export const getAllConsumer =() => get(`consumer/allConsumer`);
//注册
export const SignUp =(params) => post(`consumer/add`,params);
//登录
export const loginIn =(params) => post(`consumer/login`,params);
//根据用户id查询该用户的详细信息
export const getUserOfid = (id) => get(`consumer/selectById?id=${id}`);
//更新用户信息
export const updateUserMsg =(params) => post(`consumer/update`,params);
//====================下载相关======================
//下载音乐
export const download = (url) => Axios({
    method:'get',
    url:url,
    responseType:'bolb'
});

//====================评价相关======================
//提交评价
export const setRank = (params) => get(`rank/add`,params);
//获取指定歌单平均分
export const getRankOfSongListId = (songListId) => get(`rank/sum?songListId=${songListId}`);

//====================评论相关======================
//提交评论
export const setComment = (params) => post(`comment/add`,params);
//点赞
export const setLike = (params) => post(`comment/like`,params);
//返回当前歌单或歌曲的评论列表
export const getAllComment =(type,id) => {
    if(type == 0){  //歌曲
        return get(`comment/commentOfSongId?songId=${id}`);
    }else{  //type=1歌单
        return get(`comment/commentOfSongListId?songListId=${id}`);
    }
}

//====================收藏相关======================
//添加收藏
export const setCollect = (params) => post(`collect/add`,params);
//指定用户收藏列表
export const getCollectOfUserId = (userId) => get(`collect/collectOfUserId?userId=${userId}`);



