<template>
    <div class="song-list">
        <ul class="song-list-header">
            <li v-for="(item,index) in songStyle" :key="index" @click="handleChangeView(item.name)" :class="{active:item.name==activeName}">
                {{item.name}}
            </li>
        </ul>
        <div>
            <content-list :contentList="data"></content-list>
            <div class="pagination">
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                        :current-page="currentPage" :page-size="pageSize" :total="albumDatas.length">
                    </el-pagination>
            </div>            
        </div>


    </div>
</template>
<script>
import ContentList from '../components/ContentList'
import {getAllSongList,getSongListOfLikeStyle} from '../api/index'
import {mixin} from '../mixins'
import {songStyle} from '../assets/data/songList'
export default {
    name: 'song-list',
    components:{
        ContentList
    },
    data(){
        return{
            albumDatas: [], //歌单的数据
            pageSize: 10,   //一页展示的数据
            currentPage: 1,  //当前页 默认第一页
            songStyle, //歌单风格
            activeName: '全部歌单', //默认歌单页面的标记 高亮
        }
    },
    mounted(){
        this.songStyle = songStyle;
        this.getSongList();
    },
    computed:{
        //计算当前表格中的数据
        data(){
           return this.albumDatas.slice((this.currentPage - 1) * this.pageSize,this.currentPage * this.pageSize);
        }
    },
    methods:{
        getSongList(){
                getAllSongList()
                    .then(res =>{
                        this.currentPage = 1;                        
                        this.albumDatas = res;
                })
        },
        //获取当前页
        handleCurrentChange(val){           
            this.currentPage = val;
        },
        //根据style显示对应的歌单
        handleChangeView(name){
            this.activeName = name;
            this.albumDatas =  [];
            if(name == '全部歌单'){
                this.getSongList();
            }else{
                this.getSongListOfStyle(name);
            }
        },
        //根据style
        getSongListOfStyle(style){
            getSongListOfLikeStyle(style).then(res =>{
                this.currentPage = 1;
                this.albumDatas = res ; 
            })
        }
    }
}

</script>

<style lang="scss" scoped>
@import '../assets/css/song-list.scss';
</style>


